.menu {
    box-sizing: border-box;
    height: 100vh;
    background-color: #C2C2C2;
    font-weight: 700;
    text-transform: uppercase;
}

.menu__top {
    margin-bottom: 24px;
}
  
.menu__nav {
    margin-bottom: 40px;
}
  
.menu__content {
    padding-inline: 20px;
}
  
.menu__phone__number{
    color: inherit;
    text-decoration: none;
    display: block;
    font-size: 16px;
    line-height: 27px;
    font-weight: 600;
    margin-bottom: 16px;
}

.menu__book__a__test__ride {
    color: inherit;
    text-decoration: none;
    display: block;
    letter-spacing: 2px;
    line-height: 16px;
    font-size: 12px;
    font-weight: 600;
    border-bottom: 1px solid #1D1D1D;
    padding-bottom: 8px;
    width: fit-content;
}
