.page {
    font-family: Poppins, sans-serif;
    scroll-behavior: smooth;
    color: #1D1D1D;
}
  
.page__body {
    margin: 0;
}
  
.page__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
}
  
.page__menu:target {
    opacity: 1;
    pointer-events: all;
}
